module.exports = parseOptions;

var getUserAgent = require('universal-user-agent');

var pick = require('lodash.pick');

var pkg = require('../package.json');

var OPTION_NAMES = ['timeout', 'baseUrl', 'agent', 'headers'];

function parseOptions(userOptions) {
  if (!userOptions) {
    userOptions = {};
  }

  if (userOptions.headers) {
    userOptions.headers = Object.keys(userOptions.headers).reduce(function (newObj, key) {
      newObj[key.toLowerCase()] = userOptions.headers[key];
      return newObj;
    }, {});
  }

  var options = pick(userOptions, OPTION_NAMES);
  var clientDefaults = {
    headers: options.headers || {},
    request: {}
  };

  if (options.baseUrl) {
    clientDefaults.baseUrl = options.baseUrl;
  }

  if (options.timeout) {
    clientDefaults.request.timeout = options.timeout;
  }

  if (options.agent) {
    clientDefaults.request.agent = options.agent;
  }

  var userAgentOption = clientDefaults.headers['user-agent'];
  var defaultUserAgent = "octokit.js/".concat(pkg.version, " ").concat(getUserAgent());
  clientDefaults.headers['user-agent'] = [userAgentOption, defaultUserAgent].filter(Boolean).join(' ');
  return clientDefaults;
}