module.exports = Octokit;

var endpoint = require('@octokit/request').endpoint;

var Hook = require('before-after-hook');

var parseClientOptions = require('./parse-client-options');

var requestWithDefaults = require('./request-with-defaults');

function Octokit(plugins, options) {
  var hook = new Hook();
  var api = {
    hook: hook,
    request: requestWithDefaults(hook, endpoint, parseClientOptions(options))
  };
  plugins.forEach(function (pluginFunction) {
    return pluginFunction(api, options);
  });
  return api;
}